
import {
  React,
  connect,
  Link,
  reduxForm,
  Panel,
  PanelHeader,
} from "../../components/helpers/library";
import FilterCucian from "./FilterCucian";

const maptostate = (state) => {
  return {
    getDataTimbangan: state.utility.getTimbangan,
  };
};

class Cucian extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
    };

  }

  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Cucian</Link>
          </li>
          <li className="breadcrumb-item active">Cucian</li>
        </ol>
        <h1 className="page-header">Cucian </h1>
        <Panel>
          <PanelHeader>Cucian</PanelHeader>
          <br />
          {/* Cucian */}
          <div className="container">
            <FilterCucian />
          </div>
          <br />
          {/* End Tambah Cucian  */}
        </Panel>
      </div>
    );
  }
}

Cucian = reduxForm({
  form: "Cucian",
  enableReinitialize: true,
})(Cucian);
export default connect(maptostate,null)(Cucian);