import {
  // inputGroup,
  ReanderField,
  ReanderSelect,
} from "../../components/helpers/field";
import {
  React,
  connect,
  reduxForm,
  Field,
} from "../../components/helpers/library";

const proses_transaksi = [
  {
    value: "After Proses",
    name: "after_proses",
  },
  {
    value: "Befor Proses",
    name: "befor_proses",
  },
];


class FilterCucian extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
    };
  }

  render() {
    return (
      <div className="row">
        <div className="col-3">
          <Field
            name="no_sortir"
            component={ReanderField}
            type="text"
            label="No Sortir"
            placeholder="No Sortir"
          />
        </div>
        <div className="col-3">
          <Field
            name="no_transaksi"
            component={ReanderSelect}
            option={[]}
            type="text"
            label="No Transaksi"
            placeholder="No Transaksi"
          />
        </div>
        <div className="col-3">
          <Field
            name="proses_transaksi"
            component={ReanderSelect}
            options={proses_transaksi}
            type="text"
            label="Proses Transaksi"
            placeholder="Pilih Proses Transaksi"
          />
        </div>
      </div>
    );
  }
}

FilterCucian = reduxForm({
  form: "FilterCucian",
  enableReinitialize: true,
})(FilterCucian);
export default connect(null)(FilterCucian);
